import Banks from '../../components/banks.vue'
import ChencOrder from '../../components/checkOrder.vue'
export default {
  components: {
    Counter,
    Selection,
    CheckChooser,
    MyDialog: Dialog,
    Banks,
    ChencOrder
  },
  data () {
    return {
      isShowCheckDialog: false,
      isShowSuccess: false,
      bankId: null,
      isShowPay: false,
      buyNum: 0,
      versions: {},
