<template>
  <div class="sales-board">
      <div class="sales-board-intro">
        <h2></h2>
        <p></p>
      </div>
      <div class="sales-board-form">
        <div class="sales-board-line">
          <div class="sales-board-line-left">
                  
          </div>
          <div class="sales-board-line-right">
            <Counter @on-change="onParamChange('buyNum', $event)"></Counter>
          </div>
        </div>
        <div class="sales-board-line">
          <div class="sales-board-line-left">
                  
          </div>
          <div class="sales-board-line-right">
            <Selection :selections="buyTypes" @on-change="onParamChange('buyType', $event)"></Selection>
          </div>
        </div>
        <div class="sales-board-line">
          <div class="sales-board-line-left">
                  
          </div>
          <div class="sales-board-line-right">
            <Chooser :selections="periodList"  @on-change="onParamChange('period', $event)"></Chooser>
          </div>
        </div>
        <div class="sales-board-line">
          <div class="sales-board-line-left">
                  
          </div>
          <div class="sales-board-line-right">
            <Chooser :selections="versionList"  @on-change="onParamChange('versions', $event)"></Chooser>
          </div>
        </div>
        <div class="sales-board-line">
          <div class="sales-board-line-left">
                  
          </div>
          <div class="sales-board-line-right">
                 500
          </div>
        </div>
        <div class="sales-board-line">
          <div class="sales-board-line-left">&nbsp;</div>
          <div class="sales-board-line-right">
            <div class="button" @click="pay">
                    
            </div>
          </div>
        </div>
      </div>
      <div class="sales-board-des">
        <h2></h2>
        <p></p>

        <h3></h3>
        <ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>

        <h3></h3>
        <ul>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </div>
      <my-dialog :isShow="isShowPay" @on-change="closePay">
        <table class="buy-dialog-table">
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          <tr>
            <td>{{ buyNum }}</td>
            <td>{{ buyType.label }}</td>
            <td>{{ period.label }}</td>
            <td>{{versions.label}}</td>
            <td>500</td>
          </tr>
        </table>
        <h3 class="buy-dialog-title"></h3>
        <Banks @on-change="onChangeBanks"></Banks>
        <div class="button buy-dialog-btn" @click="confirmBuy">
          
        </div>
      </my-dialog>
      <chenc-order :is-show-check-dialog="isShowCheckDialog" @on-change="succeed" :isShowSuccess="isShowSuccess"></chenc-order>
  </div>
</template>
<script>
import _ from 'lodash'
import Selection from '../../components/selection.vue'
import Counter from '../../components/counter.vue'
import Chooser from '../../components/chooser.vue'
import Dialog from '../../components/base/dialog.vue'
import Banks from '../../components/banks.vue'
import ChencOrder from '../../components/checkOrder.vue'
export default {
  components: {
    Selection,
    Counter,
    Chooser,
    MyDialog: Dialog,
    Banks,
    ChencOrder
  },
  data () {
    return {
      isShowCheckDialog: false,
      isShowSuccess: false,
      bankId: null,
      isShowPay: false,
      buyNum: 0,
      buyType: {},
      versions: {},
      period: {},
      price: 0,
      buyTypes: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 2
        }
      ],
      versionList: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 2
        }
      ],
      periodList: [
        {
          label: '',
          value: 0
        },
        {
          label: '',
          value: 1
        },
        {
          label: '',
          value: 2
        }
      ]
    }
  },
  mounted () {
    this.buyNum = 1
    this.buyType = this.buyTypes[0]
    this.versions = this.versionList[0]
    this.period = this.periodList[0]
  },
  methods: {
    pay () {
      this.isShowPay = true
    },
    closePay () {
      this.isShowPay = false
    },
    onParamChange (attr, val) {
      this[attr] = val
    },
    onChangeBanks (bankObj) {
      this.bankId = bankObj.id
    },
    confirmBuy () {
      this.isShowPay = false
      this.isShowCheckDialog = true
    },
    succeed () {
      this.isShowCheckDialog = false
      this.isShowSuccess = true
    }
  }
}
</script>
<style scoped>
.buy-dialog-title {
  font-size: 16px;
  font-weight: bold;
}
.buy-dialog-btn {
  margin-top: 20px;
}
.buy-dialog-table {
  width: 100%;
  margin-bottom: 20px;
}
.buy-dialog-table td,
.buy-dialog-table th{
  border: 1px solid #e3e3e3;
  text-align: center;
  padding: 5px 0;
}
.buy-dialog-table th {
  background: #4fc08d;
  color: #fff;
  border: 1px solid #4fc08d;
}
</style>




