import Dialog from '../../components/base/dialog.vue'
import Banks from '../../components/banks.vue'
import ChencOrder from '../../components/checkOrder.vue'
export default {
  components: {
    Chooser,
    Selection,
    MyDialog: Dialog,
    Banks,
