    }
  },
  mounted () {
    this.buyType = this.buyTypes[0]
  },
  methods: {
    pay () {
      this.isShowPay = true
    },
    closePay () {
      this.isShowPay = false
    },
    onParamChange (attr, val) {
      this[attr] = val
    },
    onChangeBanks (bankObj) {
      this.bankId = bankObj.id
    },
    confirmBuy () {
      this.isShowPay = false
      this.isShowCheckDialog = true
    },
    succeed () {
      this.isShowCheckDialog = false
      this.isShowSuccess = true
    }
  }
}
